<template>
<div>
  <h1>TodoList</h1>
  <h5>Welcome to Shinyook's TodoListApp</h5>
  <div class="container">
    <div>
      <b-row>
        <b-col sm="10">
          <b-form-group
            id="fieldset-horizontal"
            label-cols-sm="4"
            label-cols-lg="3"
            label="What to do?"
            label-for="input-horizontal"
          >
          <b-form-input id="input-horizontal" v-model="todo.content"></b-form-input>
          </b-form-group>
        </b-col>
        <b-col sm="2">
          <b-button variant="outline-primary" @click="addTodo" @keyup.enter="addtodo">Add</b-button>
        </b-col>
      </b-row>

    <b-list-group v-if="todoItem && todoItem.length">
      <b-list-group-item
      v-for="todo of todoItem"
      v-bind:data="todo.content"
      v-bind:key="todo.create_date">
      </b-list-group-item>
    </b-list-group>
    </div>
  </div>

</div>
</template>

<script>
import axios from 'axios';
export default {
created() {
  axios.get('/api/boards')
        .then((res) => {
          this.todoItem = res.data.map(r => r.data);
          console.log(res.data);
        })
        .catch(err => {
          console.error(err);
        })
},
data() {
  return {
    todoItem: [],
    todo: {
      content: ''
    }
  }
},
methods: {
  addTodo() {
    axios.post('/api/boards', {board: this.todo})
    .then(() => {
      console.log("success!")
    })
    .catch(err => {
      console.error(err);
    })
  }
}
}
</script>

<style>
.container {
  padding-top: 50px;
  margin: 0 auto;
  width: 700px;
}
</style>



